import type { MovieType } from "@/types/global";
import Link from "next/link";

export default function Movie({
  movie,
  currentGenreId,
}: {
  movie: MovieType;
  currentGenreId?: string;
}) {
  const poster = "http://image.tmdb.org/t/p/w185";

  return (
    <div
      key={movie.id}
      className="flex flex-col gap-1 text-center w-[185px] mb-4"
    >
      <Link href={`/movie/${movie.id}?genreId=${currentGenreId}`}>
        <img
          src={poster + movie.poster_path}
          className="w-[200px] h-[260px] hover:scale-105 transition-all rounded-xl"
        />
      </Link>
      <b className="text-wrap">{movie.title}</b>
      <span>{movie.release_date.split("-")[0]}</span>
    </div>
  );
}
